html, body {
  font-weight: 300; }

a, a:visited {
  color: #FF5D51;
  text-decoration: none;
  -webkit-transition: color 150ms linear;
          transition: color 150ms linear;
  -webkit-transition: background-color 150ms linear;
          transition: background-color 150ms linear; }

a:hover {
  color: #510909;
  text-decoration: none; }

header#masthead {
  padding: 0.3em 0;
  border-bottom: 1px #e0e0e0 solid; }

nav#site-navigation {
  text-align: center; }

footer#colophon {
  border-top: 1px #e0e0e0 solid;
  text-align: center;
  padding-top: 1em;
  padding-bottom: 1em; }

.site-title {
  text-align: center;
  text-decoration: none;
  font-size: 4em;
  font-weight: 100;
  margin: 0; }
  .site-title span {
    color: #2d2d2d; }
  .site-title a {
    color: #FF5D51; }

article.podcast {
  background-color: #ededed;
  padding: 10px; }

a.main-link:hover .podcast-billboard h2 {
  padding-bottom: 20px; }

.podcast-billboard {
  width: 100%;
  height: 200px;
  display: block;
  position: relative;
  overflow: hidden; }
  .podcast-billboard h2 {
    position: absolute;
    bottom: 0px;
    width: 100%;
    font-weight: 300;
    left: 0px;
    padding: 10px;
    margin: 0px;
    color: white;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: all 200ms ease;
            transition: all 200ms ease; }
    .podcast-billboard h2 a, .podcast-billboard h2 a:visited, .podcast-billboard h2 a:focus {
      color: white; }
    .podcast-billboard h2 i {
      vertical-align: middle;
      margin-right: 8px; }
  .podcast-billboard .blur-img img {
    min-width: 120%;
    -webkit-filter: blur(8px);
            filter: blur(8px);
    margin: -10px; }
  .podcast-billboard .billboard-bg {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    width: 100%;
    height: 100%; }
  .podcast-billboard .album-art {
    height: 200px;
    width: 200px;
    position: absolute;
    right: 0px;
    top: 0px;
    box-shadow: -8px 0px 25px 0px rgba(0, 0, 0, 0.67);
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover; }
  .podcast-billboard .billboard-content {
    padding-right: 160px; }

.podcast-meta {
  background-color: #2d2d2d;
  padding: 10px;
  display: block;
  color: white; }
  .podcast-meta p {
    margin: 0;
    display: block; }
  .podcast-meta a {
    color: white;
    border: 1px white solid;
    padding: 5px 8px;
    border-radius: 4px;
    line-height: 1em;
    font-size: 0.9em; }
    .podcast-meta a i {
      margin-right: 5px; }
  .podcast-meta a:hover {
    color: white;
    background-color: #FF5D51;
    border: 1px #FF5D51 solid; }

.entry-content {
  margin-top: 0px; }
